import React, {Component} from 'react';
import Header from '../../components/header';
import Categories from '../../components/categories';
import Footer from '../../components/footer';

import './page-warranty.sass';


export default class PageWarranty extends Component {

  constructor() {
    super();
    this.state = {
      dropdownIsHidden: true
    };
  }

  updateData = (value) => {
    this.setState({ dropdownIsHidden: value })
  }


  render() {

    return (
        <div>
          <Header updateData={this.updateData}/>
          <div className="page-wrapper">
            <main className="page static-page">
              <div className="row">
                <div className="static-page-sidebar">
                  <div hidden={this.state.dropdownIsHidden} className="dropdown-hidden">
                    <Categories />
                  </div>
                </div>
                <div className="static-page-content">
                  <h3>Гарантия</h3>
                  <h5>
                    Могу ли я обменять или вернуть товар?
                  </h5>
                  <p>
                    Да, вы можете обменять или вернуть товар в течение 14 дней с момента покупки. Это право гарантирует вам «Закон о защите прав потребителя».
                    <br/><br/>
                    Чтобы использовать эту возможность, пожалуйста, убедитесь что:
                    <br/>
                    -товар, не был в употреблении и не имеет следов использования: царапин, сколов, потёртостей, на счётчике телефона не более 5 минут разговоров, программное обеспечение не подвергалось изменениям и т. п.;<br/>
                    -товар полностью укомплектован и не нарушена целостность упаковки;<br/>
                    -сохранены все ярлыки и заводская маркировка. <br/>
                    <br/>
                    То есть вы имеете право обменять непродовольственный товар надлежащего качества на аналогичный, если товар не удовлетворил вас по форме, габаритам, фасону, цвету, размеру или по другим причинам не может быть вами использованный по назначению. Обмен товара надлежащего качества осуществляется, если он не использовался и если сохранен его товарный вид, потребительские свойства, пломбы, ярлыки, а также расчетный документ, выданный потребителю вместе с проданным товаром.
                    <br/><br/>
                    В случае, если вы обратились в течении 14 дней с момента покупки с просьбой обменять товар ненадлежащего качества по вашему мнению, но товар соответствует критериям товара надлежащего качества, в таком случае Продавец производит обмен товара.
                    <br/><br/>
                    Но если товар не работает, обмен или возврат товара производится только при наличии заключения/акта сервисного центра, авторизованного производителем Товара, о том, что условия эксплуатации не нарушены.
                  </p>
                  <h5>
                    Где и как можно произвести обмен или возврат товара?
                  </h5>
                  <p>Обменять или вернуть товар можно в магазине, в котором вы приобрели Товар.
                    <br/>
                    При себе вам нужно иметь товар, упаковку и комплектующие к нему, гарантийный талон и товарный чек.
                  </p>
                  <h5>
                    Куда обращаться по вопросам гарантийного обслуживания?
                  </h5>
                  <p>
                    В случае, если Товар потребует гарантийного обслуживания, просим вас обратиться к Продавцу у которого вы приобрели изделие или в сервисный центр, контактные данные которого вы можете найти в гарантийном талоне либо уточняйте по тел. +38 099 99 99 999 (пн. - cб. с 10:00 до 20:00)
                  </p>
                </div>
              </div>
            </main>
          </div>
          <Footer/>
        </div>
    );
  }
}

