import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import {
  itemAddedToCart,
  itemRemovedFromCart,
  allItemsRemovedFromCart } from '../../actions';

import './cart-modal.sass';

const CartModal = ({ items, total, onIncrease, onDecrease, onDelete }) => {
  const renderRow = (item, idx) => {
    const { id, code, title, options, count, total } = item;
    let pictureUrl;
    if(options) {
      const selectedOption = options.find((option) => option.optionId === 0);
      pictureUrl = selectedOption.pictureUrl
    }

    return (
      <li key={idx} className="cart-item">
        <div className="preview-img">
          <img
            src={pictureUrl}
            alt="product"/>
        </div>
        <div className="preview-body">
          <div className="preview-text">
            <Link to={'/item-page/' + code}>{title}</Link>
          </div>
          <span className="preview-counter">{count} шт</span>
          <span className="preview-price">{total} грн </span>
          <div className="preview-actions">
            <button
              onClick={() => onDecrease(id)}
              className="btn-clear ondecrease preview-action">
            </button>
            <button
              onClick={() => onIncrease(id)}
              className="btn-clear onincrease preview-action">
            </button>
            <button
              onClick={() => onDelete(id)}
              className="btn-clear ondelete preview-action">
            </button>
          </div>
        </div>
      </li>
    );
  };

  let seeTotal = (
    <h3 className="blank-total">
      Самое время добавить первый товар!
    </h3>
  )

  if (items.length !== 0) {
    seeTotal = (
      <div className="total">
        <span>Сумма: {total} грн</span>
        <button className="buy-now cart-btn">Оформить заказ</button>
      </div>
    );
  }

  return (
    <div className="cart-modal-window">
      <h2>Корзина</h2>
      {items.map(renderRow)}
      {seeTotal}
    </div>
  );
};

const mapStateToProps = ({ shoppingCart: { cartItems, orderTotal }}) => {
  return {
    items: cartItems,
    total: orderTotal
  };
};

const mapDispatchToProps = {
  onIncrease: itemAddedToCart,
  onDecrease: itemRemovedFromCart,
  onDelete: allItemsRemovedFromCart
};

export default connect(mapStateToProps, mapDispatchToProps)(CartModal);
