import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './categories.sass';

export default class ItemPage extends Component {

  render() {

    return (
        <aside className="categories">
          <nav>
            <ul>
              <li><Link to="/shop-page/smartphones" className="category">Смартфоны</Link></li>
              <li><Link to="/shop-page/notebooks" className="category">Ноутбуки</Link></li>
              <li><Link to="/shop-page/watches" className="category">Смарт-часы</Link></li>
              <li><Link to="/shop-page/audio" className="category">Аудио</Link></li>
              <li><Link to="/shop-page/fitness" className="category">Фитнес и здоровье</Link></li>
              <li><Link to="/shop-page/tv" className="category">Телевизоры</Link></li>
              <li><Link to="/shop-page/accessories" className="category">Аксессуары</Link></li>
            </ul>
          </nav>
        </aside>
    )
  }
}
