import React, {Component} from 'react';
import Spinner from '../spinner';
import ErrorIndicator from '../error-indicator';
import Header from '../header';
import Categories from '../categories';
import Sidebar from '../sidebar';
import Item from '../item';
import Selected from '../selected';
import Footer from '../footer';

import './shop-page.sass';

import { connect } from 'react-redux';

import { withStoreService } from '../hoc';
import { fetchItems, itemAddedToCart, itemAddedToFavs } from '../../actions';
import { compose } from '../../utils';
import Snackbar from "../snackbar";


const ItemsList = ({ items, favsItems, onAddedToCart, onAddedToFavs, onSnackbar }) => {
  return (
    <div className="items-block">
      {
        items.map((item) => {

          let selected = false;
          // eslint-disable-next-line
          favsItems.map((elem) => {
            if (elem.code === item.code) {
              selected = true;
            }
          });

          return (
            <Item
              key={item.id}
              item={item}
              onAddedToCart={() => onAddedToCart(item.id)}
              onAddedToFavs={() => onAddedToFavs(item.id)}
              onSnackbar={onSnackbar}
              selected={selected}
            />
          );
        })
      }
    </div>
  );
};


class ShopPage extends Component {

  constructor() {
    super();
    this.state = {
      dropdownIsHidden: true,
      sidenavIsHidden: true,
      value: {
        min: 0,
        max: 1
      },
      removeSelectedRange: 1111,
    };
  }

  componentDidMount() {
    this.props.fetchItems();
  }

  updateRange = (value) => {
     this.setState({
       value: value
     })
  }

  updateData = (value) => {
     this.setState({ dropdownIsHidden: value })
  }

  filterCategory = (items, categoryId) => {
    return items.filter(item => item.category === categoryId);
  }

  filterPrice = (items) => {
    let min = this.state.value.min;
    let max = this.state.value.max;
    if (max !== 1) {
      return items.filter((item) => (item.price >= min)&&(item.price <= max));
    } else {
      return items;
    }
  };

  findMaxPrice = (items) => {
    let maxPrice = 0;
    // eslint-disable-next-line
    items.map((item) => {
      if (item.price > maxPrice) {
        maxPrice = item.price
      }
    })
    return maxPrice;
  }

  translateCategory = (categoryId) => {
    switch (categoryId) {
      case 'smartphones':
        return 'Смартфоны';
      case 'notebooks':
        return 'Ноутбуки';
      case 'watches':
        return 'Смарт-часы';
      case 'audio':
        return 'Аудио';
      case 'fitness':
        return 'Фитнес и здоровье';
      case 'tv':
        return 'Телевизоры';
      case 'accessories':
        return 'Аксессуары';
      default:
        return 'Категория';
    }
  }

  toggleSidenav = () => {
    this.setState({ sidenavIsHidden: !this.state.sidenavIsHidden });
  }

  removeSelectedRange = (value) => {
    this.setState({ removeSelectedRange: value })
  }

  snackbarRef = React.createRef();

  _showSnackbarHandler = () => {
    this.showSnackbar();
  }

  showSnackbar() {
    this.snackbarRef.current.openSnackBar('Товар добавлен в корзину!');
  }

  render() {

    const { items, favsItems, loading, error, onAddedToCart, onAddedToFavs } = this.props;

    let categoryId = this.props.match.params.category_id;
    const categoryItems = this.filterCategory(items, categoryId);

    let maxPrice = this.findMaxPrice(categoryItems);

    const visibleItems = this.filterPrice(categoryItems);

    const categoryH2 = this.translateCategory(categoryId);

    if (loading) {
      return <Spinner />;
    }

    if (error) {
      return <ErrorIndicator />;
    }

    let selected = (
        <Selected maxPrice={maxPrice}/>
    )

    if (this.state.value.max !== 1) {
      selected = (
          <Selected selectedRange={this.state.value} removeSelectedRange={this.removeSelectedRange}/>
      )
    }

    return (
      <div className="no-overflow">
        <Header updateData={this.updateData}/>
        <Snackbar ref={this.snackbarRef} />
        <div className="page-wrapper shop-page">
          <main className="page">
            <div className="shop-page-sidebar">
              <div hidden={this.state.dropdownIsHidden} className="dropdown-hidden">
                <Categories />
              </div>
              {selected}
              <Sidebar maxPrice={maxPrice} updateRange={this.updateRange} removeSelectedRange={this.state.removeSelectedRange}/>
            </div>
            <div className="shop-page-content">
              <div hidden={this.state.dropdownIsHidden} className="sidenav-left">
                <Categories />
              </div>
              <div className="shop-content">
                <h2>{categoryH2}</h2>
                <button className="filters-btn btn-clear" onClick={this.toggleSidenav}>Фильтры</button>
                <ItemsList items={visibleItems} favsItems={favsItems} onAddedToCart={onAddedToCart} onAddedToFavs={onAddedToFavs} onSnackbar={this._showSnackbarHandler}/>
              </div>
            </div>
            <div className="catalog-overlay" hidden={this.state.sidenavIsHidden}/>
            <div className={!this.state.sidenavIsHidden ? 'sidenav-right active ': 'sidenav-right'} >
              <button className="btn-clear close-sidenav" onClick={this.toggleSidenav}/>
              {selected}
              <Sidebar maxPrice={maxPrice} updateRange={this.updateRange} removeSelectedRange={this.state.removeSelectedRange}/>
            </div>
          </main>
        </div>
        <Footer/>
      </div>
    )
  }

}

const mapStateToProps = ({ itemsList: { items, loading, error }, favsList: { favsItems }}) => {
  return { items, loading, error, favsItems };
};

const mapDispatchToProps = (dispatch, { storeService }) => {

  return {
    fetchItems: fetchItems(storeService, dispatch),
    onAddedToCart: (id) => dispatch(itemAddedToCart(id)),
    onAddedToFavs: (id) => dispatch(itemAddedToFavs(id)),
  };
};

export default compose(
  withStoreService(),
  connect(mapStateToProps, mapDispatchToProps)
)(ShopPage);
