import React, {Component} from 'react';
import Header from '../../components/header';
import Categories from '../../components/categories';
import Footer from '../../components/footer';

import './page-delivery.sass';


export default class PageDelivery extends Component {

  constructor() {
    super();
    this.state = {
      dropdownIsHidden: true
    };
  }

  updateData = (value) => {
    this.setState({ dropdownIsHidden: value })
  }


  render() {

    return (
        <div>
          <Header updateData={this.updateData}/>
          <div className="page-wrapper">
            <main className="page static-page">
              <div className="row">
                <div className="static-page-sidebar">
                  <div hidden={this.state.dropdownIsHidden} className="dropdown-hidden">
                    <Categories />
                  </div>
                </div>
                <div className="static-page-content">
                  <h3>Доставка и оплата</h3>
                  <h5>
                    Самовывоз товара из магазина
                  </h5>
                  <p>
                    Вы имеете возможность оформить заказ на интересующий вас товар на нашем сайте и забрать свою покупку в ближайшем магазине нашей компании.
                    <br/><br/>
                    При оформлении заказа оператор контакт-центра уточняет удобное для вас место и время приезда за покупкой. Доставка товара в магазин по вашему заказу осуществляется бесплатно*.
                    <br/><br/>
                    *оплата платежей, связанных с доставкой товара осуществляется за счет Продавца.
                  </p>
                  <h5>
                    До отделения «Нова пошта»
                  </h5>
                  <p>
                    Сроки доставки в отделение «Нова пошта» 1 - 3 дня.
                    <br/><br/>
                    После отправки заказа вы получите SMS-сообщение с номером экспресс-накладной. <br/>
                    Уточнить конкретную дату получения вашего заказа вы можете на сайте компании «Нова пошта»
                    <br/><br/>
                    Единая стоимость доставки в любое отделение «Нова пошта» — 25 грн.
                    <br/><br/>
                    В обязательном порядке проверяйте ваш заказ на соответствие с товарным чеком перед оплатой в отделении «Нова пошта».
                  </p>
                  <h5>
                    До отделения «Укрпошта»
                  </h5>
                  <p>
                    Сроки доставки в отделение «Укрпошта» 1 - 3 дня.
                    <br/><br/>
                    После отправки заказа вы получите SMS-сообщение с номером экспресс-накладной.
                    <br/><br/>
                    Единая стоимость доставки в любое отделение «Укрпошта» — 20 грн.
                    <br/><br/>
                    Внимание! Доставка в отделение «Укрпошта» доступна для заказов на сумму не более 15 000 грн.
                    <br/><br/>
                    В обязательном порядке проверяйте ваш заказ на соответствие с товарным чеком перед оплатой в отделении «Укрпошта».
                  </p>
                  <h5>
                    Оплата
                  </h5>
                  <p>
                    - наличными <br/>
                    - безналичная <br/>
                    - оплата наложенным платежом <br/>
                    - кредит или рассрочка <br/>
                  </p>
                </div>
              </div>
            </main>
          </div>
          <Footer/>
        </div>
    );
  }
}

