import React, { Component } from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import ShopPage from '../shop-page';
import HomePage from '../home-page';
import ItemPage from '../item-page';
import AdminPage from '../admin-page';
import Auth from '../auth';
import PageDelivery from '../../pages/page-delivery';
import PageWarranty from '../../pages/page-warranty';
import PageAboutus from '../../pages/page-aboutus';
import ScrollToTop from "../scrolltotop/scrolltotop";


export default class App extends Component {

  render() {

    return (
      <Router>
        <ScrollToTop>
          <Switch>
            <Route path="/" exact component={HomePage} />
            <Route path="/shop-page" exact component={ShopPage} />
            <Route path="/shop-page/:category_id" render={(props) => <ShopPage key={props.match.params.category_id} {...props} />} />
            <Route path="/item-page/:item_id" exact component={ItemPage} />
            <Route path="/admin-page" exact component={AdminPage} />
            <Route exact path="/auth" component={Auth}/>
            <Route path="/delivery" exact component={PageDelivery} />
            <Route path="/warranty" exact component={PageWarranty} />
            <Route path="/aboutus" exact component={PageAboutus} />

            <Route component={HomePage} />
          </Switch>
        </ScrollToTop>
      </Router>
    );
  }
}
