const updateFavsItems = (favsItems, favsItem, idx) => {

  if (idx === -1) {
    return [
      ...favsItems,
      favsItem
    ];
  }

  return [
    ...favsItems.slice(0, idx),
    ...favsItems.slice(idx + 1)
  ];
};

const updateFavsItem = (item, favsItem = {}, quantity) => {

  const {
    id = item.id,
    code = item.code,
    title = item.title,
    options = item.options,
    price = item.price
  } = favsItem;

  return {
    id,
    code,
    title,
    options,
    price
  };
};

const updateOrder = (state, itemId, quantity) => {
  const { itemsList: { items }, favsList: { favsItems }} = state;

  const item = items.find(({id}) => id === itemId);
  const favsItemIndex = favsItems.findIndex(({id}) => id === itemId);
  const favsItem = favsItems[favsItemIndex];

  const newFavsItem = updateFavsItem(item, favsItem, quantity);

  return {
    favsItems: updateFavsItems(favsItems, newFavsItem, favsItemIndex)
  };
};

const updateFavsList = (state, action) => {

  if (state === undefined) {
    return {
      favsItems: []
    }
  }

  switch(action.type) {
    case 'ITEM_ADDED_TO_FAVS':
      return updateOrder(state, action.payload, 1);

    case 'ITEM_REMOVED_FROM_FAVS':
      return updateOrder(state, action.payload, -1);

    default:
      return state.favsList;
  }
};

export default updateFavsList;
