import React, { Component } from 'react';
import { Link } from 'react-router-dom';


import './footer.sass';



export default class Footer extends Component {


  render() {

    return (
      <footer>
        <div className="benefits-wrapper">
          <div className="benefits">
            <div className="benefit">
              <div className="img-wrapper">
                <img src={require('../../assets/shipped.svg')} alt="delivery"/>
              </div>
              <p>Доставка товара по Украине</p>
            </div>
            <div className="benefit">
              <div className="img-wrapper">
                <img src={require('../../assets/security.svg')} alt="warranty"/>
              </div>
              <p>Официальная гарантия от производителя</p>
            </div>
            <div className="benefit">
              <div className="img-wrapper">
                  <img src={require('../../assets/money.svg')} alt="payment"/>
              </div>
              <p>Оплата картой или наличными</p>
            </div>
            <div className="benefit">
              <div className="img-wrapper">
                <img src={require('../../assets/return.svg')} alt="return"/>
              </div>
              <p>Возврат без вопросов</p>
            </div>
          </div>
        </div>
        <div className="main-footer-wrapper">
          <div className="main-footer">
            <div className="call-block">
              <h6>Оформить заказ</h6>
              <a className="number" href="tel:380999999999">+38 099 99 99 999</a>
              <span>c 10:00 до 20:00 (пн-сб)</span>
            </div>
            <nav>
              <ul>
                <li><Link to="/delivery">Доставка и оплата</Link></li>
                <li><Link to="/warranty">Гарантия</Link></li>
                <li><Link to="/aboutus">О компании</Link></li>
              </ul>
            </nav>
            <div className="join-block">
              <p>Присоединяйтесь</p>
              <ul>
                <a href="https://www.facebook.com/" target="_blank" rel="noopener noreferrer"><li><img src={require('../../assets/facebook-logo.svg')} alt="facebook"/></li></a>
                <a href="https://www.instagram.com/" target="_blank" rel="noopener noreferrer"><li><img src={require('../../assets/instagram-logo.svg')} alt="instagram"/></li></a>
                <a href="https://telegram.org/" target="_blank" rel="noopener noreferrer"><li><img src={require('../../assets/telegram-logo.svg')} alt="telegram"/></li></a>
              </ul>
            </div>
          </div>
          <div className="secondary-footer">
            <span className="about-us"><Link to="/">&copy; New Project 2019</Link></span>
            <span className="about-developer">Дизайн и разработка - <a href="https://www.instagram.com/r1ruslan/" target="_blank" rel="noopener noreferrer">R1Ruslan</a></span>
          </div>
        </div>
      </footer>
    )
  }
}
