import React from 'react';
import { Link } from 'react-router-dom';

import './item.sass';


const Item = ({ item, selected, onAddedToCart, onAddedToFavs, onSnackbar }) => {

  const { title, price, code, options } = item;

  let elem = document.getElementById(code);
  if (elem) {
    let el = document.getElementById(code).classList;
    if (selected) {
      el.add("fav-selected");
    } else {
      el.remove("fav-selected");
    }
  }

  let sliced = title.slice(0,50);
  if (sliced.length < title.length) {
    sliced += '...';
  }

  let pictureUrl;
  if(options) {
    const selectedOption = options.find((option) => option.optionId === 0);
    pictureUrl = selectedOption.pictureUrl
  }

  return (
    <div className="card">
      <button className="btn-clear fav-check" onClick={onAddedToFavs} id={code}/>
      <Link to={'/item-page/' + code}>
        <div className="img-wrapper">
          <img
            src={pictureUrl}
            alt={title}/>
         </div>
      </Link>
      <div className="card-body">
        <Link to={'/item-page/' + code}>{sliced}</Link>
      </div>
      <div className="price">{price} грн</div>
      <div className="card-info">
        <div className="isExist"><img src={require('../../assets/verified.svg')} alt="yes"/><span>В наличии</span></div>
        <button className="btn-clear small-buy"
                onClick={()=>{ onAddedToCart(); onSnackbar() }}
        >Купить</button>
      </div>
    </div>
  );
};

export default Item;
