import React, { Component } from 'react';

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import './carousel-slider.sass';

export default class CarouselSlider extends Component {

  render() {

    return (
      <div className="carousel-wrapper">
        <Carousel
          showThumbs={false}
          infiniteLoop={true}
          useKeyboardArrows={true}
          autoPlay={true}
          interval={5000}
          transitionTime={500}
          emulateTouch={true}
          showStatus={false}
        >
          <div className="inner-slide">
            <img src="https://www.apple.com/v/home/ek/images/heroes/iphone-xr/main__bmngiblug0mq_largetall.jpg" alt="slider"/>
          </div>
          <div className="inner-slide">
            <img src="https://www.apple.com/v/macbook-air/d/images/meta/og.png" alt="slider"/>
          </div>
          <div className="inner-slide">
            {/*<img src="https://www.ahstatic.com/photos/6157_de_01_p_2048x1536.jpg" alt="slider"/>*/}
            <img src="https://www.apple.com/v/apple-watch-nike/m/images/overview/training_club_app__cm8kinp6z4wi_large.jpg" alt="slider"/>
          </div>
          <div className="inner-slide">
            <img src="https://www.apple.com/ru/ipad/home/images/overview/ipad_air__de99jh4astea_large.jpg " alt="slider"/>
          </div>
          <div className="inner-slide">
            <img src="https://www.apple.com/euro/support/products/mac/a/screens_alt/images/mac-hero_2x.png" alt="slider"/>
          </div>
        </Carousel>
      </div>
    )
  }
}
