const updateCartItems = (cartItems, cartItem, idx) => {

  if (cartItem.count === 0) {
    return [
      ...cartItems.slice(0, idx),
      ...cartItems.slice(idx + 1)
    ];
  }

  if (idx === -1) {
    return [
      ...cartItems,
      cartItem
    ];
  }

  return [
    ...cartItems.slice(0, idx),
    cartItem,
    ...cartItems.slice(idx + 1)
  ];
};

const updateCartItem = (item, cartItem = {}, quantity) => {

  const {
    id = item.id,
    code = item.code,
    title = item.title,
    options = item.options,
    count = 0,
    total = 0
  } = cartItem;

  return {
    id,
    code,
    title,
    options,
    count: count + quantity,
    total: total + quantity * item.price
  };
};

const updateOrder = (state, itemId, quantity) => {
  const { itemsList: { items }, shoppingCart: { cartItems }} = state;

  const item = items.find(({id}) => id === itemId);
  const cartItemIndex = cartItems.findIndex(({id}) => id === itemId);
  const cartItem = cartItems[cartItemIndex];

  const newCartItem = updateCartItem(item, cartItem, quantity);

  const finalCartItems = updateCartItems(cartItems, newCartItem, cartItemIndex);
  let orderTotal = 0;
  // eslint-disable-next-line
  finalCartItems.map((item) => {
    orderTotal += item.total;
  });

  return {
    orderTotal: orderTotal,
    cartItems: finalCartItems
  };
};

const updateShoppingCart = (state, action) => {

  if (state === undefined) {
    return {
      cartItems: [],
      orderTotal: 0
    }
  }

  switch(action.type) {
    case 'ITEM_ADDED_TO_CART':
      return updateOrder(state, action.payload, 1);

    case 'ITEM_REMOVED_FROM_CART':
      return updateOrder(state, action.payload, -1);

    case 'ALL_ITEMS_REMOVED_FROM_CART':
      const item = state.shoppingCart.cartItems.find(({id}) => id === action.payload);
      return updateOrder(state, action.payload, -item.count);

    default:
      return state.shoppingCart;
  }
};

export default updateShoppingCart;
