import React, {Component} from 'react';
import Header from '../../components/header';
import Categories from '../../components/categories';
import Footer from '../../components/footer';

import './page-aboutus.sass';


export default class PageAboutus extends Component {

  constructor() {
    super();
    this.state = {
      dropdownIsHidden: true
    };
  }

  updateData = (value) => {
    this.setState({ dropdownIsHidden: value })
  }


  render() {

    return (
        <div>
          <Header updateData={this.updateData}/>
          <div className="page-wrapper">
            <main className="page static-page">
              <div className="row">
                <div className="static-page-sidebar">
                  <div hidden={this.state.dropdownIsHidden} className="dropdown-hidden">
                    <Categories />
                  </div>
                </div>
                <div className="static-page-content">
                  <h3>О компании</h3>
                  <h5>
                    Маленькие мечты и грандиозные планы
                  </h5>
                  <p>
                    New Project – один из крупнейших онлайн-ритейлеров в стране. Уже 5 лет мы воплощаем маленькие мечты и грандиозные планы миллионов людей. У нас можно найти буквально все. Мы продаем по справедливой цене и предоставляем гарантию, так как считаем, что онлайн-шопинг должен быть максимально удобным и безопасным. И каждый раз, когда кто-то нажимает «Купить», мы понимаем, что делаем нужное дело.
                  </p>
                </div>
              </div>
            </main>
          </div>
          <Footer/>
        </div>
    );
  }
}

