import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//import firebase from '../../firebase.js';

import './auth.sass';

export default class Auth extends Component {

  constructor() {
    super();
    this.state = {
      password: '',
      access: false
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    this.setState({
      [e.target.name] : e.target.value
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    if (this.state.password === 'r1music') {
      this.setState({
        access: true
      })
    }
    this.setState({
      password: ''
    });

  }




  render() {


    let done = (
      <div></div>
    );
    if (this.state.access === true) {
      done = (
        <div>
          <Link to="/admin-page">Add product</Link>
        </div>
      );
    }

    return (
      <div>
        <Link to="/">Home</Link>
        <form className="add-item" onSubmit={this.handleSubmit}>
          <span>Fill in with a password</span>
          <input type="password" placeholder="enter your password" name="password" required onChange={this.handleChange} value={this.state.password} />
          <button>Log In</button>
        </form>
        {done}
      </div>
    );
  }
}
