import React, {Component} from 'react';
import { Link } from 'react-router-dom';

import Spinner from '../spinner';
import ErrorIndicator from '../error-indicator';
import Header from '../header';
import Categories from '../categories';
import Snackbar from '../snackbar';
import Footer from '../footer';

import './item-page.sass';

import { connect } from 'react-redux';
import { withStoreService } from '../hoc';
import { fetchItems, itemAddedToCart, itemAddedToFavs } from '../../actions';
import { compose } from '../../utils';


class ItemPage extends Component {

  constructor() {
    super();
    this.state = {
      item: [],
      dropdownIsHidden: true,
      optionId: 0,
      onFavs: false
    };
  }

  componentDidMount() {
    // this.props.fetchItems();
    let itemId = this.props.match.params.item_id;
    // console.log('here is itemid ' + itemId);
    let item = this.findItem(this.props.items, itemId);
    this.setState({
      item: item
    });

    this.onDidMountFavs(item);
  }

  componentDidUpdate() {
    let itemId = this.props.match.params.item_id;
    // console.log('update itemid is ' + itemId);
    if (this.state.item.code !== itemId) {
      let item = this.findItem(this.props.items, itemId);
      this.setState({
        item: item
      });
      this.onDidMountFavs(item);
    }
  }

  findItem(items, code) {
    if (!code) return [];
    const result = items.find(item => item.code === code);
    return result;
  }

  updateData = (value) => {
     this.setState({ dropdownIsHidden: value })
  }

  onDidMountFavs(fav) {
    const result = this.props.favsItems.find(item => item.code === fav.code);
    if (result) {
      this.setState({
        onFavs: true
      })
    }
  }

  onChangeFavs() {
    this.setState({
      onFavs: !this.state.onFavs
    })
  }

  translateCategory = (categoryId) => {
    switch (categoryId) {
      case 'smartphones':
        return 'Смартфоны';
      case 'notebooks':
        return 'Ноутбуки';
      case 'watches':
        return 'Смарт-часы';
      case 'audio':
        return 'Аудио';
      case 'fitness':
        return 'Фитнес и здоровье';
      case 'tv':
        return 'Телевизоры';
      case 'accessories':
        return 'Аксессуары';
      default:
        return 'Категория';
    }
  }

  snackbarRef = React.createRef();

  _showSnackbarHandler = () => {
    this.snackbarRef.current.openSnackBar('Товар добавлен в корзину!');
  }

  changeOption = (id) => {
    this.setState({
      optionId: id
    })
  };

  render() {

    const { id, code, title, price, category, brand, options } = this.state.item;
    let optionsList;
    let pictureUrl;

    const categoryH2 = this.translateCategory(category);

    if (this.props.loading) {
      return <Spinner />;
    }

    if (this.props.error) {
      return <ErrorIndicator />;
    }

    if (options) {
      optionsList = (
          <div className="custom-color-picker">
            {options.map((option) =>
              <div key={option.optionId}>
                <input type="radio"
                       id={option.color}
                       name="color"
                       value={option.color}
                       defaultChecked={option.optionId === 0}
                       onClick={() => this.changeOption(option.optionId)}
                />
                <label htmlFor={option.color} data-toggle="tooltip" data-placement="top" title={option.color}>
                  <span></span>
                </label>
              </div>
            )}
          </div>
      )
    }

    if(options) {
      const selectedOption = options.find((option) => option.optionId === this.state.optionId);
      pictureUrl = selectedOption.pictureUrl
    }


    return (
      <div>
        <Header updateData={this.updateData}/>
        <div hidden={this.state.dropdownIsHidden} className="categories-dropdown">
          <Categories />
        </div>
        <Snackbar ref = {this.snackbarRef} />
        <div className="page-wrapper">
          <main className="page">
            <div className="column-12">
              <aside className="item-navigation">
                <nav className="breadcrumbs">
                  <ul>
                    <li><Link to="/"><img src={require('../../assets/home.svg')} alt="home"/></Link></li>
                    <li><Link to={'/shop-page/' + category}>{categoryH2}</Link></li>
                    <li><Link to="#" className="capitalize">{brand}</Link></li>
                  </ul>
                </nav>
                <span className="item-code">
                  Код: {code}
                </span>
              </aside>
              <article className="item-content">
                <div className="top-text-block">
                  <h1>{title}</h1>
                  <div className="options-picker">
                    <div className="color-picker">
                      <p>Цвет</p>
                      {optionsList}
                    </div>

                    <div className="model-picker no-display">
                      <p>Модель</p>
                      <div className="custom-model-picker">
                        <div>
                          <input type="radio" id="color-4" name="color" value="color-4"  />
                          <label htmlFor="color-4">
                            <span>256 Гб</span>
                          </label>
                        </div>
                        <div>
                          <input type="radio" id="color-5" name="color" value="color-5" />
                          <label htmlFor="color-5">
                            <span>512 Гб</span>
                          </label>
                        </div>
                      </div>
                    </div>

                  </div>
                </div>

                <div className="img-block">
                  <div className="img-wrapper">
                    <img
                      src={pictureUrl}
                      alt={title}/>
                  </div>
                </div>

                <div className="bottom-text-block">
                  <span className="price">{price} грн</span>
                  <div className="buttons-row">
                    <button
                      className="buy-success btn-clear"
                      onClick={()=>{ this.props.onAddedToCart(id); this._showSnackbarHandler() }}>
                      Купить
                    </button>
                    <div className="buttons-inner-row">
                      <button
                          className="buy-secondary btn-clear"
                          onClick={()=>{ this.props.onAddedToCart(id); this._showSnackbarHandler() }}>
                        Купить в кредит
                        </button>
                      <button
                        className={this.state.onFavs ? "favs-add btn-clear favs-added" : "favs-add btn-clear"}
                        onClick={()=>{ this.props.onAddedToFavs(id); this.onChangeFavs() }}>
                      </button>
                    </div>
                  </div>
                  <div className="divider"></div>
                  <form className="fast-buy">
                    <h3>Быстрый заказ</h3>
                    <span>Номер телефона:</span>
                    <input type="text" name="number" placeholder="+38 0XX XXX XX XX" required />
                    <span>Комментарий (по желанию):</span>
                    <input type="text" name="comment" placeholder="хочу золотой макбук!" />
                    <button className="buy-now btn-clear">Оформить заказ</button>
                  </form>
                </div>

              </article>
            </div>
          </main>
        </div>
        <Footer/>
      </div>
    )
  }
}

const mapStateToProps = ({ itemsList: { items, loading, error }, favsList: { favsItems }}) => {
  return { items, loading, error, favsItems };
};

const mapDispatchToProps = (dispatch, { storeService }) => {

  return {
    fetchItems: fetchItems(storeService, dispatch),
    onAddedToCart: (id) => dispatch(itemAddedToCart(id)),
    onAddedToFavs: (id) => dispatch(itemAddedToFavs(id)),
  };
};

export default compose(
  withStoreService(),
  connect(mapStateToProps, mapDispatchToProps)
)(ItemPage);
