import React, {Component} from 'react';

import InputRange from 'react-input-range';
//import firebase from '../../firebase.js';

import './sidebar.sass';
import 'react-input-range/lib/css/index.css';

export default class Sidebar extends Component{

  constructor(props) {
    super(props);

    this.state = {
      value: {
        min: 0,
        max: this.props.maxPrice
      },
      removeSelectedRange: 1111
    };
    this.onChangeInput = this.onChangeInput.bind(this);
    this.onChangeRange = this.onChangeRange.bind(this);
  }

  onChangeInput(e) {
    this.setState({
      value: {
        ...this.state.value,
        [e.target.name] : +e.target.value
      }
    });
  }

  onChangeRange(value) {
    this.setState({ value });
    this.props.updateRange(this.state.value);
  }

  blurred() {
    console.warn('keyup');
  }

  removeSelectedRange() {
    this.setState({
      value: {
        min: 0,
        max: this.props.maxPrice
      }
    });
    this.props.updateRange({min: 0, max: this.props.maxPrice})
  }

  componentDidUpdate() {
    if (this.state.removeSelectedRange !== this.props.removeSelectedRange) {
      this.removeSelectedRange();
      this.setState({
        removeSelectedRange: this.props.removeSelectedRange
      })
    }
  }


  render() {

    return (
      <div className="sidebar">
        <div className="filters-header">
          <h3>Фильтр</h3>
        </div>
        <section className="side-box">
          <h3>Цена</h3>
          <div className="roww">
            <div className="half">
              <span>от</span>
              <div className="el-input">
                <input autoComplete="off" type="number" className="el-input-inner" name="min" onChange={this.onChangeInput} value={this.state.value.min} />
              </div>
            </div>
            <div className="half">
              <span>до</span>
              <div className="el-input">
                <input autoComplete="off" type="number" className="el-input-inner" name="max" onChange={this.onChangeInput} value={this.state.value.max}/>
              </div>
            </div>
          </div>
          <button onClick={() => { this.props.updateRange(this.state.value)}} className="unique-button">Сортировать по цене</button>

          <form className="form">
            <InputRange
              draggableTrack
              minValue={0}
              maxValue={this.props.maxPrice}
              onChange={this.onChangeRange}
              value={this.state.value}
              onBlur={this.blurred}
            />
          </form>
        </section>

        <section className="side-box">
          <h3>Бренд</h3>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="apple"/>Apple</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="samsung"/>Samsung</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="huawei"/>Huawei</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="oneplus"/>OnePlus</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="xiaomi"/>Xiaomi</label>
          </div>
        </section>

        <section className="side-box no-display">
          <h3>Объем памяти</h3>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="memory1"/>256 Гб</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="memory2"/>512 Гб</label>
          </div>
          <div className="checkbox">
            <label><input type="checkbox" name="" value="memory3"/>1 Тб</label>
          </div>
        </section>


      </div>
    );
  }
}
